<template>
  <el-container>
    <el-header class="header">
      <span>MockServer</span>
    </el-header>
    <el-menu :default-openeds="['1']" mode="horizontal" @select="toMenu">
      <el-menu-item index="inter">接口一览</el-menu-item>
      <el-menu-item index="log">接口日志</el-menu-item>
      <el-menu-item index="system">系统配置</el-menu-item>
    
    </el-menu>
    <el-main>
      <iframe :src="homeSrc" frameborder="0" height="800px" width="100%"></iframe>
    </el-main>
  </el-container>
</template>
<script>
import intro from "@/components/intro"

export default {
  data() {
    return {
      frameShow: false,
      frameName: "intro",
      homeSrc: "/#/intro",
    config: {
        "log": "/#/interLog",
        "inter": "/#/inter",  
        "detail": "/#/InterDetail",
        "edit": "/#/InterEdit",
        "system": "/#/system/system"
      }
    }
  }, components: {
    intro
  }
  , methods: {
    toMenu(key, keyPath) {
      this.homeSrc = this.config[key];
    },
    openIntro() {
      this.frameShow = true;
      this.frameName = intro;
    }
  }
};
</script>

<style src="./index.css" scoped></style>




